@charset "utf-8";
/* TOC – Color Variables

- Basics
- Corporate Identity Colorpalette
- Foundation Color Variables
- Grey Scale
- Topbar-Navigation
- Footer
- Code

*/



/* Basics
------------------------------------------------------------------- */

$text-color:       	#111;
$body-font-color: 	$text-color;
$body-bg: 			#fdfdfd;

/* New ISC palatte by Bob Rigel 
-------------------------------- */

$isc-1: #BBDFC0;   // Lightest green
$isc-2: #A7CAA8;   // Mid green
$isc-3: #86AC85;   // Main green
$isc-4: #789877;   // Dark green
$isc-5: #DFD683;   // Dark sand
$isc-6: #ECEDC0;   // Light sand

$isc-yellow: #EFC94C;		// yellow
$isc-orange: #E27A3F;		// orange
$isc-red:    #DF4949;		// red

/* Foundation Color Variables
------------------------------------------------------------------- */

$primary-color: $isc-3;
$secondary-color: $isc-1;
$alert-color: $isc-red;
$success-color: $isc-3;
$warning-color: $isc-yellow;
$info-color: $isc-2;



/* Grey Scale
------------------------------------------------------------------- */

$grey-1: #E4E4E4;
$grey-2: #D7D7D7;
$grey-3: #CBCBCB;
$grey-4: #BEBEBE;
$grey-5: #A4A4A4;
$grey-6: #979797;
$grey-7: #8B8B8B;
$grey-8: #7E7E7E;
$grey-9: #646464;
$grey-10: #575757;
$grey-11: #4B4B4B;
$grey-12: #3E3E3E;
$grey-13: #313131;
$grey-14: #242424;
$grey-15: #171717;
$grey-16: #0B0B0B;



/* Topbar-Navigation
------------------------------------------------------------------- */

$topbar-bg-color: $isc-4;
$topbar-bg: $topbar-bg-color;

$topbar-dropdown-toggle-color: $isc-5;

$topbar-link-color: #FFFFFF;
$topbar-link-color-hover: #FFFFFF;
$topbar-link-color-active: #000;
$topbar-link-color-active-hover: #000;

$topbar-dropdown-label-color: $isc-6;
$topbar-dropdown-link-bg-hover: $isc-6;

$topbar-link-bg-active: $isc-6; // Active Navigation Link
$topbar-link-bg-hover: $isc-5;
$topbar-link-bg-active-hover: $isc-5;

$topbar-dropdown-bg: $isc-6; // Background Mobile Navigation
$topbar-dropdown-link-color: #000;
$topbar-dropdown-link-bg: $isc-6;

$topbar-menu-link-color: #FFFFFF;
$topbar-menu-icon-color: #FFFFFF;
$topbar-menu-link-color-toggled: $isc-1;
$topbar-menu-icon-color-toggled: $isc-1;



/* Footer
------------------------------------------------------------------- */

$footer-bg: $grey-10;
$footer-color: #fff;
$footer-link-color: $isc-4;


$subfooter-bg: $grey-13;
$subfooter-color: $grey-10;
$subfooter-link-color: $grey-10;



/* Code
------------------------------------------------------------------- */

$code-background-color: scale-color($secondary-color, $lightness: 70%);

$highlight-background: #ffffff;
$highlight-comment: #999988;
$highlight-error: #a61717;
$highlight-comment-special: #999999;
$highlight-deleted: #000000;
$highlight-error-2: #aa0000;
$highlight-literal-string: #d14;
$highlight-literal-number: #009999;
$highlight-name-attribut: #008080;
$highlight-error-background: #e3d2d2;
$highlight-generic-deleted: #ffdddd;
$highlight-generic-deleted-specific: #ffaaaa;
$highlight-generic-inserted: #ddffdd;
$highlight-generic-inserted-specific: #aaffaa;
$highlight-generic-output: #888888;
$highlight-generic-prompt: #555555;
$highlight-subheading: #aaaaaa;
$highlight-keyword-type: #445588;
$highlight-name-builtin: #0086B3;
$highlight-name-class: #445588;
$highlight-name-entity: #800080;
$highlight-name-exception: #990000;
$highlight-name-function: #990000;
$highlight-name-namespace: #555555;
$highlight-name-tag: #000080;
$highlight-text-whitespace: #bbbbbb;
$highlight-literal-string-regex: #009926;
$highlight-literal-string-symbol: #990073;
